"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Appointment } from "@/types/appwrite.types";
import { AppointmentForm } from "./forms/AppointmentForm";
import "react-datepicker/dist/react-datepicker.css";
export const AppointmentModal = ({
  patientId,
  userId,
  appointment,
  type
}: {
  patientId: string;
  userId: string;
  appointment?: Appointment;
  type: "schedule" | "cancel";
  title: string;
  description: string;
}) => {
  const [open, setOpen] = useState(false);
  return <Dialog open={open} onOpenChange={setOpen} data-sentry-element="Dialog" data-sentry-component="AppointmentModal" data-sentry-source-file="AppointmentModal.tsx">
      <DialogTrigger asChild data-sentry-element="DialogTrigger" data-sentry-source-file="AppointmentModal.tsx">
        <Button variant="ghost" className={`capitalize ${type === "schedule" && "text-green-500"}`} data-sentry-element="Button" data-sentry-source-file="AppointmentModal.tsx">
          {type}
        </Button>
      </DialogTrigger>
      <DialogContent className="shad-dialog sm:max-w-md" data-sentry-element="DialogContent" data-sentry-source-file="AppointmentModal.tsx">
        <DialogHeader className="mb-4 space-y-3" data-sentry-element="DialogHeader" data-sentry-source-file="AppointmentModal.tsx">
          <DialogTitle className="capitalize" data-sentry-element="DialogTitle" data-sentry-source-file="AppointmentModal.tsx">{type} Appointment</DialogTitle>
          <DialogDescription data-sentry-element="DialogDescription" data-sentry-source-file="AppointmentModal.tsx">
            Please fill in the following details to {type} appointment
          </DialogDescription>
        </DialogHeader>

        <AppointmentForm userId={userId} patientId={patientId} type={type} appointment={appointment} setOpen={setOpen} data-sentry-element="AppointmentForm" data-sentry-source-file="AppointmentModal.tsx" />
      </DialogContent>
    </Dialog>;
};