"use client";

import { getPaginationRowModel, ColumnDef, flexRender, getCoreRowModel, useReactTable } from "@tanstack/react-table";
import Image from "next/image";
import { redirect } from "next/navigation";
import { useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { decryptKey } from "@/lib/utils";
interface DataTableProps<TData, TValue> {
  columns: ColumnDef<TData, TValue>[];
  data: TData[];
}
export function DataTable<TData, TValue>({
  columns,
  data
}: DataTableProps<TData, TValue>) {
  const encryptedKey = typeof window !== "undefined" ? window.localStorage.getItem("accessKey") : null;
  useEffect(() => {
    const accessKey = encryptedKey && decryptKey(encryptedKey);
    if (accessKey !== process.env.NEXT_PUBLIC_ADMIN_PASSKEY!.toString()) {
      redirect("/");
    }
  }, [encryptedKey]);
  const table = useReactTable({
    data,
    columns,
    getCoreRowModel: getCoreRowModel(),
    getPaginationRowModel: getPaginationRowModel()
  });
  return <div className="data-table" data-sentry-component="DataTable" data-sentry-source-file="DataTable.tsx">
      <Table className="shad-table" data-sentry-element="Table" data-sentry-source-file="DataTable.tsx">
        <TableHeader className=" bg-dark-200" data-sentry-element="TableHeader" data-sentry-source-file="DataTable.tsx">
          {table.getHeaderGroups().map(headerGroup => <TableRow key={headerGroup.id} className="shad-table-row-header">
              {headerGroup.headers.map(header => {
            return <TableHead key={header.id}>
                    {header.isPlaceholder ? null : flexRender(header.column.columnDef.header, header.getContext())}
                  </TableHead>;
          })}
            </TableRow>)}
        </TableHeader>
        <TableBody data-sentry-element="TableBody" data-sentry-source-file="DataTable.tsx">
          {table.getRowModel().rows?.length ? table.getRowModel().rows.map(row => <TableRow key={row.id} data-state={row.getIsSelected() && "selected"} className="shad-table-row">
                {row.getVisibleCells().map(cell => <TableCell key={cell.id}>
                    {flexRender(cell.column.columnDef.cell, cell.getContext())}
                  </TableCell>)}
              </TableRow>) : <TableRow>
              <TableCell colSpan={columns.length} className="h-24 text-center">
                No results.
              </TableCell>
            </TableRow>}
        </TableBody>
      </Table>
      <div className="table-actions">
        <Button variant="outline" size="sm" onClick={() => table.previousPage()} disabled={!table.getCanPreviousPage()} className="shad-gray-btn" data-sentry-element="Button" data-sentry-source-file="DataTable.tsx">
          <Image src="/assets/icons/arrow.svg" width={24} height={24} alt="arrow" data-sentry-element="Image" data-sentry-source-file="DataTable.tsx" />
        </Button>
        <Button variant="outline" size="sm" onClick={() => table.nextPage()} disabled={!table.getCanNextPage()} className="shad-gray-btn" data-sentry-element="Button" data-sentry-source-file="DataTable.tsx">
          <Image src="/assets/icons/arrow.svg" width={24} height={24} alt="arrow " className="rotate-180" data-sentry-element="Image" data-sentry-source-file="DataTable.tsx" />
        </Button>
      </div>
    </div>;
}